<template>
<div>
  <div class="alert-container"></div>
  <div class="form-group">
      <label>Ícono</label>
      <div class="input-group">
          <input v-model="model.Icon" type="text" class="form-control" name="Icon">
          <span class="input-group-addon"><icon :name="selectedIcon"></icon></span>
      </div>
      <span data-key="Icon" class="form-validation-failed"></span>
  </div>
  <div class="form-group">
      <label>Nombre</label>
      <input v-model="model.Name" type="text" class="form-control" name="Name">
      <span data-key="Name" class="form-validation-failed"></span>
  </div>
</div>
</template>

<script>
import icon from './global.icon.vue'
export default {
  components: {
    icon
  },
  data() {
    return {
      model: {
        Icon: 'code',
        Name: ''
      }
    }
  },
  name: 'category',
  props: {
    id: {
      type: Number,
      default: 0
    },
    url: {
      type: String,
      required: true
    }
  },
  mounted() {
    this.$parent.$on('categorySelectedID', function(id) {
      debugger;
      this.id = id;
    })
  },
  watch: {
    id(val) {
      console.log('here')
      var self = this;
      if(val > 0) {
        $.post(self.url, {
          id: self.id
        }, function(r) {
          self.model.Icon = r.Icon;
          self.model.Name = r.Name;
        }, 'json')
      } else {
        
      }
    }
  }
}
</script>


